/* main */
body {
  font-family: "Helvetica Neue", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: #000;
  background-color: #fff; }

.container {
  margin: 0 auto;
  max-width: 1440px;
  width: 100%;
  padding: 0 64px 0 35px; }

/* components */
.header {
  padding-top: 64px; }
  .header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header-logo {
    width: 184px; }
  .header-controls {
    display: flex;
    align-items: center; }
    .header-controls__phone {
      margin-right: 146px; }
    .header-controls__audio {
      position: relative; }
      .header-controls__audio-item {
        position: absolute;
        bottom: -100px;
        right: 0;
        width: 0;
        height: 0;
        opacity: 0;
        pointer-events: none; }
      .header-controls__audio-icon {
        opacity: 0.5;
        transition: 0.3s ease-in-out;
        cursor: pointer; }
        .header-controls__audio-icon.active {
          opacity: 1; }

.footer-inner {
  position: relative; }

.footer-anim {
  max-width: 1254px;
  width: 100%; }

.footer-wrapper {
  position: absolute;
  top: 104px;
  right: 35px;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  width: 954px;
  padding: 32px 53px 69px 32px;
  color: #fff;
  background-color: #58634f; }

.footer-title {
  max-width: 300px; }
  .footer-title__item {
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px; }
  .footer-title__sup {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px; }

.footer-info__link {
  max-width: 317px; }
  .footer-info__link:not(:last-child) {
    margin-bottom: 30px; }

.link {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 20px;
  text-decoration: none;
  color: #000;
  transition: 0.3s ease-in-out; }
  @media only screen and (min-width: 1024px) {
    .link:hover {
      color: #58634f; } }
  .link--inverse {
    color: #fff; }
    @media only screen and (min-width: 1024px) {
      .link--inverse:hover {
        color: #000; } }
  .link--no {
    align-items: flex-start; }
    @media only screen and (min-width: 1024px) {
      .link--no:hover {
        color: inherit; } }
  .link__icon {
    flex-shrink: 0;
    margin-right: 21px; }

